<template>
	<div class="wrapper" v-bind:class="theme === 'light' ? themeClasses[0] : themeClasses[1]">
		<img :src="require(`@/assets/svg/${theme === 'light' ? pathToThemeIcons[1] : pathToThemeIcons[0]}.svg`)" @click="toggleTheme" id="theme-icon">
	</div>
</template>
 
<script>
export default {
	data() {
		return {
			themeClasses: ['light-theme', 'dark-theme'],
			pathToThemeIcons: ['sun', 'moon'],
			theme: 'light',
		}
	},
	methods: {
		toggleTheme() {
			this.theme === 'light' ? this.theme = 'dark' : this.theme = 'light'
		}
	}
}
</script>

<style>

@import './assets/nullstyle.css';

.wrapper {
	width: 100%;
	height: 100vh;
}

.wrapper.light-theme {
	background-color: white;
	color: black;
}

.wrapper.dark-theme {
	background-color: black;
	color: white;
}

#theme-icon {
	position: absolute;
	top: 20px;
	right: 20px;
	cursor: pointer;
}

</style>

